
body{
    font-family: 'Lato','sans-serif';
    height: 1000px;
  
}
nav{
    background: white;
}
nav li:last-child{
    background: #FE5F1E;
}
/* nav li:last-child:hover{
    background: #b23301;
} */
nav li a{
    transition: .3s all ease-in-out;
}
nav li a:hover{
    background: #FE5F1E;
}
nav li a:last-child:hover{
    background: transparent;
}
nav .logo{
    max-width: 80px;
}

.rnd{
    border-radius: 25px;
}
nav li a, nav li a img{
    outline: none;

}
section.hero{
    background: #F8F8F8;

}
.addcart{
    border: 2px solid #FE5F1E;
    color: #FE5F1E;
    outline: none !important;
    border: none !important;
}
.noout{
    
    outline: none !important;
    border: none !important;
}
.cartnum{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}
.addcart:hover{
    background: #FE5F1E;
    color: white;
}
.mrb{
    border-color: transparent !important;
    line-height: 50px !important;
}
.btn{
    background: #FE5F1E;
}
.btn:hover{
    background: #b23301;
}
.d-col{
    display: flex;
    flex-direction: column;
}
.empty-cart{
    background: #F8F8F8;
}
.olist{
    border-radius: 12px;
    height: 11rem;
    width: 15rem;
}
.amount{
    color: #FE5F1E;
}
section.login{
    background: #F8F8F8;
    min-height: calc(100vh - 86px);
}
section.login a{
    color: #FE5F1E;
}
section.login a:hover{
    color: #b23301;
}
section.status{
   background-color:  #F8F8F8;
   min-height: calc(100vh - 88px);
}
.status-box{
    padding: 60px 0 0;
}
.mynavs{
    position: fixed;
    width: 100vw;
    overflow: hidden;
}
.status-box ul li span{
    position: relative;
    padding-left: 20px;
}
.status-box ul li span::after{
    content: '';
    position: absolute;
    height: 10px;
    width: 10px;
    border-radius: 50%;
    left: -10px;
    top: 50%;
    background: #232323;
    transform: translateY(-50%);

}
.status-box ul li span::before{
    
    font-size: 46px;
    font-weight: 600;
    position: absolute;
    left: -90px;
    top: 50%;
    transform: translateY(-50%);
    font-family: 'Line Awesome Free';
    color: inherit;
}
.status-box ul li:nth-child(1) span::before{
    content: '\f46c';
}

.status-box ul li:nth-child(2) span::before{
    content: '\f560';
}

.status-box ul li:nth-child(3) span::before{
    content: '\f818';
}

.status-box ul li:nth-child(4) span::before{
    content: '\f0d1';
}

.status-box ul li:nth-child(5) span::before{
    content: '\f582';
}
.status-box ul li.step-c::before{
    background: rgb(241, 155, 177) !important;
}
.status-box ul li.step-c span::after{
    background: rgb(241, 155, 177) !important;
}
.status-box ul li.step-c span{
    color: rgb(241, 155, 177) !important;
}
.status-box ul li small{
    float: right;
    font-size: 16px;
    margin-top: 8px;
}
.del{
    
  width: 36px !important;
  height: 36px !important;
  display: flex !important;
  flex-direction: row !important;
  justify-content: center !important;
  align-items: center;
  font-size: 5px !important;
  border-radius: 50% !important;
  margin: 0 15px 6px 3px !important;
  cursor: pointer;
  padding: 5px !important;
  background-color: purple;
  transform: translateX(15px);
  color: white;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, .3);
}
@media (max-width:1060px) {
    .container{
        max-width: 90%;
    }
    
    table{
        
        overflow-x: auto;
        display: block;
    }
    td{
        border-right: 30px solid;
        border-color: transparent;
    }
    
}